import React, { Component, Fragment } from 'react';
import {Context} from '../../contextclass';
import './propping.css'

class Chat extends Component {    
    constructor(props){
        super(props);

        this.state = {
            id: localStorage.getItem("id"),
            players: [],
            messages: [],
            fectchplayers: false,
            runfetchmessages: true
        }
    }

    static contextType = Context;

    componentDidMount(){
        const propping_id = this.props.name[0];
        const prize = this.props.name[2];
        const credit = prize/10;
        this.setState({ propping_id: propping_id });
        this.setState({ credit: credit }, () => {
            this.subtractCredit( this.state.id );
        });
        this.fectchPlayers(propping_id);
        this.runFetchMessages();
    }

    subtractCredit = ( id ) => {
        const credit = this.state.credit;

        fetch('https://immense-sierra-36950.herokuapp.com/subtractcredit', {
            method: 'POST',
            body: JSON.stringify({ id: id, credit: credit }),
            headers: { 
                'Content-Type': 'application/json',
                'Accept': 'application/json' 
            }
        })
        .then(res => res.json())
        .then((body) => (body.response_msg === "Subtracting credit successful!" ? console.log( credit+" subtracted successfully!" ) : console.log("could not subtract credit")));
    }

    fectchPlayers = ( propping_id ) =>{
        //do settimeout to check periodically
        this.setState({ propping_id: propping_id });
        fetch('https://immense-sierra-36950.herokuapp.com/players', {
            method: 'POST',
            body: JSON.stringify({ propping_id: propping_id }),
            headers: { 
                'Content-Type': 'application/json',
                'Accept': 'application/json' 
            }
        })
        .then(res => res.json())
        .then((body) => (body.response_msg === "Here are the players!" ? this.setState({ players: body.players }) : console.log("could not retrieve players")));
    }

    fectchMessages = ( propping_id ) =>{
        //do settimeout to check periodically
        fetch('https://immense-sierra-36950.herokuapp.com/fetchmessages', {
            method: 'POST',
            body: JSON.stringify({ propping_id: propping_id }),
            headers: { 
                'Content-Type': 'application/json',
                'Accept': 'application/json' 
            }
        })
        .then(res => res.json())
        .then((body) => (body.response_msg === "Here are the messages!" ? this.setState( { messages: body.messages } ) /*this.setState({ messages: body.messages })*/ : console.log("could not retrieve messages")));
    }

    runFetchMessages = () => {
        console.log("We're in messages:");
        if( this.props.name[1] > 2 ){
            setTimeout(() => {
                //this.setState( { messages: this.state.messages[0].messages} )   
                this.fectchMessages(this.props.name[0]);
                this.runFetchMessages();
            }, 2000);
        }else{
            return ;
        }
    } 

    render(){ 
        return (
            <Fragment>
                <div className="messages">
                    { this.state.messages.map((item, index) =>
                        <div key={ index } className="message">
                            { item.name+' | ' }{ item.message }
                        </div>
                    )}
                </div>
                <div className="players">
                    { this.state.players.map((item, index) =>
                    <div key={ index } className="player">
                        <div className="name">
                            <img src={ process.env.PUBLIC_URL+"/uploads/profileimages/default.png" } alt="profile" />
                            { item.displayedname }
                        </div>
                        <div className="vote">
                        </div>
                    </div>
                    )}
                </div>
            </Fragment>
        );
    }
}

export default Chat;