import React, { createContext } from 'react';

//export const Context = createContext();
export const Context = createContext();

export const Provider = props => {
    let auth= "Sign Up";
    
    const data = {
        id: "init id",
        name: localStorage.getItem("name"),
        authenticated: localStorage.getItem("authenticated")
    };

    let players = [];

    let fetchplayers = false;

    let propping_id = "";

        return (
            <Context.Provider value={[data, (id, auth) => {data.id = id; data.authenticated = auth;}, players, fetchplayers, propping_id, auth, (name) => {data.name = name;}]} >
                {props.children}
            </Context.Provider>
        );

}
