import React, { Component } from 'react';
import './templates.css';
import {Link} from 'react-router-dom';
import {Context} from '../../contextclass';

class HeaderRight extends Component{
    constructor(props){
        super(props);

        this.state = {
            auth: "",
            name: ""
        }
    }
    
    static contextType = Context;

    authenticate = () => {
        let name = localStorage.getItem("name");
        if( this.context[0].athenticated === true || this.context[0].athenticated === "true" ){
            this.setState({ auth: true }, () => {
                if( this.state.auth === true || this.state.auth === "true" ){
                    document.location = "./play";
                    console.log("headerright's balling!"+this.state.auth);
                }
            });
        }else if( this.context[0].athenticated === false || this.context[0].athenticated === "false" ){
        this.setState({ auth: false }, () => {
            if( this.state.auth === false || this.state.auth === "false" ){
                document.location = "./";
                console.log("headerright's!"+this.state.auth);
            }
        });
        }

        this.setState({ "name": name });
    }

    signout = () => {
        localStorage.setItem("authenticated", false);
        localStorage.removeItem("id");
        localStorage.removeItem("name");
        localStorage.removeItem("token");
        this.setState({ auth: localStorage.getItem("authenticated") }, () => {
            this.context[1]("", false);
            if( this.state.auth === "false" || this.state.auth === false ){
                document.location = "./";
            }
        });
        
        console.log("auth is now: "+this.state.auth);
    }

    activateForceUpdate = () => {
        this.setState( { auth: true } );
        this.setState( { name: localStorage.getItem("name") } );
    }

    componentDidMount(){
        this.setState( { auth: this.context[0].authenticated } );
    }

    render(){
        if( this.state.auth === "true" || this.state.auth === true ){
            console.log("THIS IS WHAT AUTH HAS BECOME: "+this.state.auth);
            return ( 
                <div className="header-right">
                    <div style={ { display: "none" } }>
                        <button id="updateHeaderRight" onClick={ this.activateForceUpdate }></button>
                    </div>
                    <div className="name"><img src={ process.env.PUBLIC_URL+"/uploads/profileimages/default.png" } alt="profile pic" />{ localStorage.getItem("name") }</div>
                    <div className="sign-out" onClick={ this.signout }>Sign Out</div>
                </div>
            )
        }else{
            return (
            <div className="header-right">
                <div style={ { display: "none" } }>
                    <button id="updateHeaderRight" onClick={ this.activateForceUpdate }></button>
                </div>
                <div className="sign-up"><Link to="./signup">Sign Up</Link></div>
                <div className="sign-in"><Link to="./">Sign In</Link></div>
            </div>
            );
        }
    }
}

export default HeaderRight;