import React, { Component, Fragment } from 'react';
import { Redirect, Link } from 'react-router-dom';
import {Context} from '../../contextclass';
import './user-auth.css';

class Login extends Component{

    constructor(props){
        super(props);

        this.state = {
            id: "",
            email: "",
            password: "",
            name: "",
            token: "",
            error: "",
            isauthenticated: false
        };

       this.handleChange = this.handleChange.bind(this);
    }

    static contextType = Context;

    handleChange(event){
        let id = event.target.id;
        let value = event.target.value;
        this.setState({[id]: value});     
    }

    authenticate = event => {
        event.preventDefault();

        fetch('https://immense-sierra-36950.herokuapp.com/auth', {
            method: 'POST',
            body: JSON.stringify({ email: this.state.email, password: this.state.password }),
            headers: { 'Content-Type': 'application/json' }
        })
        .then(res => res.json())
        .then((body) => (body.response_msg === "You are authorized to proceed!" ? this.setState({id: body.id, name: body.name, token: body.token, isauthenticated: true}) : this.setState({error: "The email or password is incorrect."})));
    }
    
    componentDidMount(){
        console.log("context in login: "+this.context[0].authenticated)
    }

    render() {                
        if(this.state.isauthenticated === true){
            localStorage.setItem("id", this.state.id);
            localStorage.setItem("name", this.state.name);
            localStorage.setItem("authenticated", "true");
            localStorage.setItem("token", this.state.token);
            this.context[1](this.state.id, true);
            this.context[6](this.state.name);
            document.getElementById("updateHeaderRight").click();
            return (<div>
                <Redirect to={{
                    pathname: '/play',
                    state: ""
                }} />
            </div>);
        }else{
            return (
                <Fragment>
                    <div className="auth-scontainer" style={{backgroundImage: "url("+process.env.PUBLIC_URL+"/background.png)", backgroundRepeat: "no-repeat", backgroundSize: "100%"}}>
                    <div className="succint-hiw">
                        Watch an ad for the chance to win some money for yourself and a charity.
                    </div>
                    <div className="auth">
                        <div className="error_msg">{ this.state.error }</div>
                        <form onSubmit={ this.authenticate } className="form-signin">
                        <h5 className="h3 mb-3 font-weight-normal">Please sign in</h5>
                        <label className="sr-only">Email address</label>
                        <input type="email" id="email" className="form-control" placeholder="Email address" onChange={ this.handleChange } required autoFocus />
                        <label className="sr-only">Password</label>
                        <input type="password" id="password" className="form-control" placeholder="Password" onChange={ this.handleChange } required />
                        <div className="checkbox mb-3">
                            <label>
                            <input type="checkbox" value="remember-me" /> Remember me
                            </label>
                        </div>

                        <input type="submit" className="btn btn-primary" value="Sign In" />
                        </form>

                        <Link to="/signup">Create an account!</Link>
                        
                    </div>

                    <div className="hiw">
                        <h4>How it works!</h4>
                        <p>Sign in, watch an ad to earn some money credits, put yours and 9 other people's credits in a pool and vote for who takes it all, cash out and give any percentage of your winnings (could be 1% could be 100%) to a selected charity, that's it. Even if someone else wins a round, you have just contributed towards a good charity. You've done some good. And also, you can play as many times as you want.</p>
                    </div>
                    </div>
                </Fragment>
            );
        }
    }
    
  }
  
export default Login;