import React, { Component, Fragment } from 'react';
import { Link } from 'react-router-dom';

class Signup extends Component{

  constructor(props){
      super(props);

      this.state = {
        email: "",
        password: "",
        firstname: "",
        lastname: "",
        displayedname: "",
        gender: "",
        dateofbirth: "",
        error: ""
      };

      this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event){
    let id = event.target.id;
    let value = event.target.value;
    this.setState({[id]: value});
  }

  signup = (event) => {
      event.preventDefault();
      
      fetch('https://immense-sierra-36950.herokuapp.com/signup', {
          method: 'POST',
          body: JSON.stringify({ 
            email: this.state.email, 
            password: this.state.password,
            firstname: this.state.firstname, 
            lastname: this.state.lastname,
            displayedname: this.state.displayedname, 
            dateofbirth: this.state.dateofbirth,
            gender: this.state.gender 
          }),
          headers: { 'Content-Type': 'application/json' }
      })
      .then(res => res.json())
      .then((body) => (body.response_msg === "You were just signed up!" ? this.movingToAd(body.id) : this.setState({ "error": body.response_msg })))
      .catch((error) => {
        console.log("Error message: "+error);
      });
  }

  movingToAd = (id) => {
    localStorage.setItem("authenticated", true);
    localStorage.setItem("token", 1);
    localStorage.setItem("id", id );
    window.location = "/play";
  }

    render() {
      return (
        <Fragment>
        <div className="auth-scontainer" style={{backgroundImage: "url("+process.env.PUBLIC_URL+"/background.png)", backgroundRepeat: "no-repeat", backgroundSize: "100%"}}>
            <div className="auth">
                <div className="error_msg">{ this.state.error }</div>
                <form onSubmit={ this.signup } className="form-signup">
                <h5 className="h3 mb-3 font-weight-normal">Sign up for Cariprop</h5>
                <label className="sr-only">Email address</label>
                <input type="email" id="email" className="form-control" placeholder="Email address" onChange={ this.handleChange } required autoFocus />
                <label className="sr-only">Password</label>
                <input type="password" id="password" className="form-control" placeholder="Password" onChange={ this.handleChange } required />
                <label className="sr-only">First Name</label>
                <input type="text" id="firstname" className="form-control" placeholder="First Name" onChange={ this.handleChange } required />
                <label className="sr-only">Last Name</label>
                <input type="text" id="lastname" className="form-control" placeholder="Last Name" onChange={ this.handleChange } required />
                <label className="sr-only">Displayed Name</label>
                <input type="text" id="displayedname" className="form-control" placeholder="Displayed Name (What others will see)" onChange={ this.handleChange } required />
                <select id="gender" className="custom-select" onChange={ this.handleChange } required>
                  <option defaultValue>Gender</option>
                  <option></option>
                  <option>Female</option>
                  <option>Male</option>
                </select>
                <label className="sr-only">Date of Birth</label>
                <input type="date" id="dateofbirth" className="form-control" placeholder="MM/DD/YYYY" onChange={ this.handleChange } required />
                <div className="checkbox mb-3">
                    <label>
                    By signing up you agree to the <Link to="./terms" target="_blank">Terms of Use and Privacy Policies of Cariprop</Link>.
                    </label>
                </div>

                <input type="submit" className="btn btn-primary" value="Sign Up" />
                </form>
                <br />
                <br />
                
                <Link to="/">Back Sign In</Link>
            </div>
          </div>
      </Fragment>);
    }
    
  }

  export default Signup;