import React, { Component, Fragment } from 'react';
import './propping.css';

class Comment extends Component {
    constructor(props){
        super(props);

        this.state = {
            message: ""
        }
    }

    setMessage = (e) => {
        const message = e.target.value;
        this.setState( { message: message } )
    }

    message = () => {
        const id = localStorage.getItem("id");
        const name = localStorage.getItem("name");
        const message = document.getElementById('message').value;
        const propping_id = this.props.name;
        
        document.getElementById('message').value = "";
        
        fetch('https://immense-sierra-36950.herokuapp.com/message', {
            method: 'POST',
            body: JSON.stringify({ id: id, name: name, message: message, propping_id: propping_id }),
            headers: { 
                'Content-Type': 'application/json',
                'Accept': 'application/json' 
            }
        })
        .then(res => res.json())
        .then((body) => (body.response_msg === "Message was successfully inserted!" ? console.log("Message sent!") : console.log("could not retrieve players")));
    }

    render(){ 
        return (
            <Fragment>
                <input type="text" onKeyPress={ this.setMessage } id="message" className="comment_input" placeholder="Say something!" />
                <input type="button" onClick={ this.message }  className="btn btn-primary" value=">>" />                        
            </Fragment>
        );
    }
}

export default Comment;